function getRandomInt(min, max) {
  min = Math.ceil(min);
  max = Math.floor(max);
  return Math.floor(Math.random() * (max - min + 1)) + min;
}

const random = (array) => {
  return eval(array)[Math.floor(Math.random() * eval(array).length)]
}

