import React, { useState } from 'react'
import { Link } from 'react-router-dom'
import Tabs from './global/Tabs'
import Controls from './global/Controls'
import BackArrowButton from './global/buttons/BackArrowButton'
import NextArrowButton from './global/buttons/NextArrowButton'
import { Turquoise } from './global/colors'
import AcceptOrRejectButtons from './global/buttons/AcceptOrRejectButtons'
import InlineIconLabel from './global/InlineIconLabel'
import { randomCandidate } from './global/mock'
function JobApplicants() {
  const [index, setIndex] = useState(0)

  let data = {
    newMatches: [
      randomCandidate(),
      randomCandidate(),
      randomCandidate(),
      randomCandidate(),
      randomCandidate(),
      randomCandidate(),
      randomCandidate(),
    ]
  }

  const [showMore, setShowMore] = useState({})

  const setShowMoreClick = (name, showMoreState) => {
    console.log(showMore[name])
    let copy = showMore
    copy[name] = showMoreState
    setShowMore(copy)
  }

  return (
    <div>
      <Controls
        left={<Link to='/jobs-overview'><BackArrowButton label='Jobs Overview' /></Link>}
        right={<NextArrowButton label='Java Developer' />}
      />
      <h1>Frontend Developer</h1>
      <div className='columns'>
        <div className='column is-8'>
          <Tabs
            tabs={['New Matches (8)', 'In Process (4)', 'Candidates Accepted (4)']}
            position={index}
            setPosition={setIndex}
          />
        </div>
      </div>


      {index === 0 && data.newMatches && data.newMatches.map(newMatch => (
        <div style={{ borderBottom: '1px solid #E4E4E4', marginBottom: 24 }}>
          <div className='columns is-gapless is-marginless'>
            <div className='column is-1'>
              <figure className="image" style={{ width: 64, height: 64, margin: 'auto' }}>
                <img
                  className="is-rounded"
                  src={newMatch.profile_picture}
                  alt=''
                  style={{ objectFit: 'cover', objectPosition: 'center center', width: '100%', height: '100%' }}
                />
              </figure>
            </div>
            <div className='column is-9'>
              <div>
                <div>
                  <h3 style={{ display: 'inline', marginRight: 12 }}>{newMatch.name}</h3>
                  <InlineIconLabel icon='fas fa-map-marker' color='#D1D1D6'>
                    {newMatch.location}
                  </InlineIconLabel>
                </div>
                <div>
                  {newMatch.tech_stack && newMatch.tech_stack.slice(0, 3).map(tech => (
                    <div style={{
                      backgroundColor: `${tech.experience === 3 ? '#7F4CDC' : ''}${tech.experience === 2 ? '#A079E5' : ''}${tech.experience === 1 ? '#CCB8F1' : ''}`,
                      display: 'inline-block',
                      color: '#FFFFFF',
                      fontWeight: 'bold',
                      margin: 4,
                      paddingTop: 4,
                      paddingBottom: 4,
                      paddingLeft: 12,
                      paddingRight: 12,
                    }}>{tech.name}</div>
                  ))}
                </div>
                <div>
                  {!showMore[newMatch.name] && newMatch.work && newMatch.work.slice(0, 2).map(work => (
                    <div>{work.role} - {work.company.name} | {work.start_date} - {work.end_date}</div>
                  ))}

                  {!showMore[newMatch.name] && newMatch.work && newMatch.work.length > 2 && <div
                    style={{ fontWeight: 'bold', cursor: 'pointer', color: Turquoise }}
                    onClick={event => setShowMoreClick(newMatch.name, !showMore[newMatch.name])}
                  >
                    Show {newMatch.work.length - 2} More Experiences <i className='fas fa-chevron-down' />
                  </div>}

                  {showMore[newMatch.name] && newMatch.work && newMatch.work.map(work => (
                    <div>{work.role} - {work.company.name} | {work.start_date} - {work.end_date}</div>
                  ))}

                  {showMore[newMatch.name] && newMatch.work && newMatch.work.length > 2 && <div
                    style={{ fontWeight: 'bold', cursor: 'pointer', color: Turquoise }}
                    onClick={event => setShowMoreClick(newMatch.name, !showMore[newMatch.name])}
                  >
                    Show Less Experiences <i className='fas fa-chevron-up' />
                  </div>}
                </div>
              </div>
            </div>
            <div className='column is-2' style={{ textAlign: 'right', marginTop: 16 }}>
              <Controls
                right={[
                  <AcceptOrRejectButtons />
                ]}
              />
            </div>
          </div>
        </div>
      ))}

    </div >
  )
}

export default JobApplicants