import React, { useState } from 'react'
import IconLabelButton from './IconLabelButton'
import { Red, Turquoise } from '../colors'

function AcceptOrRejectButtons() {
  const [accept, setAccept] = useState(false)
  const [reject, setReject] = useState(false)

  const clickAccept = () => {
    if (!accept && !reject) {
      setAccept(!accept)
    } else if (reject) {
      setReject(!reject)
    } else {
      setAccept(!accept)
    }
  }

  const clickReject = () => {
    if (!accept && !reject) {
      setReject(!reject)
    } else if (accept) {
      setAccept(!accept)
    } else {
      setReject(!reject)
    }
  }

  return (
    <div className='columns is-gapless is-marginless'>
      <div className='column is-6' style={{ textAlign: 'right' }}>
        <AcceptButton active={accept} onClick={() => clickAccept()} />
      </div>
      <div className='column is-6' style={{ textAlign: 'left' }}>
        <RejectButton active={reject} onClick={() => clickReject()} />
      </div>
    </div>
  );
}

export default AcceptOrRejectButtons

function AcceptButton(params) {
  return (
    <IconLabelButton
      icon='fas fa-check-circle'
      style={{ fontSize: 24, color: `${params.active ? Turquoise : '#D1D1D6'}` }}
      onClick={params.onClick}
    />
  )
}

function RejectButton(params) {
  return (
    <IconLabelButton
      icon='fas fa-times-circle'
      style={{ fontSize: 24, color: `${params.active ? Red : '#D1D1D6'}` }}
      onClick={params.onClick}
    />
  )
}